<style>
  .store-check-wraper {
    border: 1px solid #ececec;
    border-radius: 8px;
    padding: 8px;
    width: 530px;
    box-sizing: border-box;
  }

  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .del-store-icon-w {
    position: absolute;
    top: -8px;
    right: 2px;
    cursor: pointer;
  }
</style>
<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMesg" label-width="140px">
        <el-form-item label="活动类型" required>
          <p style="color: #222;font-size: 16px; font-weight: bold;">{{addParamMesg.activityTypeName}}</p>
        </el-form-item>
        <region class="region-wrap" className="add-input" label="选择区域" :required="true"></region>
        <el-form-item label="活动名称" required>
          <el-input v-model="addParamMesg.activityName" size="small" class="add-input" placeholder="请输入活动名称" clearable></el-input>
          <span>活动名称,限制30个汉字</span>
        </el-form-item>
        <el-form-item label="开始时间" required>
          <el-date-picker
            class="add-input"
            v-model="addParamMesg.activityStartDate"
            type="datetime"
            placeholder="请选择活动开始时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            :picker-options="pickerOptions">
          </el-date-picker>
          <span>活动开始时间</span>
        </el-form-item>
        <el-form-item label="结束时间" required>
          <el-date-picker
            class="add-input"
            v-model="addParamMesg.activityEndDate"
            type="datetime"
            placeholder="请选择活动结束时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            :picker-options="pickerOptions">
          </el-date-picker>
          <span>活动结束时间</span>
        </el-form-item>
        <el-form-item label="配送方式" required>
          <div>
            <el-checkbox v-model="canDelivery">支持配送</el-checkbox>
            <el-radio-group v-model="deliveryTypeTemp" v-if="canDelivery" style="margin-left: 30px;">
              <el-radio :label="1">支持外卖配送</el-radio>
              <el-radio :label="2">支持快递配送</el-radio>
            </el-radio-group>
          </div>
          <div>
            <el-checkbox v-model="canPickSelf">支持自提</el-checkbox>
          </div>
        </el-form-item>
        <el-form-item label="参与门店" required>
          <el-button size="small" @click="chooseStoreOpen" icon="el-icon-plus">选择门店</el-button>
          <div v-if="activityStoresCheckedList.length > 0" class="store-check-wraper">
            <el-table
              class="fixed-height"
              :data="activityStoresCheckedList"
              max-height="300"
              size="small">
              <el-table-column min-width="220px" label="门店编码/名称">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="80" prop="cityName" label="城市"></el-table-column>
              <el-table-column min-width="80" prop="saleTypeName" label="门店类型"></el-table-column>
              <el-table-column
                align="center"
                label="操作"
                width="100">
                <template slot-scope="scope" size="small">
                  <el-button @click="deleteStoreItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item label="参与商品" required>
          <el-button size="small" @click="chooseGoodsOpen" icon="el-icon-plus">选择商品</el-button>
          <div  v-if="activityGoodsCheckedList.length > 0">
            <el-input placeholder="拼团价折扣(0-100整数)" class="add-input" size="small" v-model="discount">
              <el-button slot="append" @click="setDiscountBatch">确定</el-button>
            </el-input>

            <el-table
              class="fixed-height"
              :data="activityGoodsCheckedList"
              empty-text="暂无选中任何商品"
              style="width:100%;"
              max-height="400"
              @select="handleSelect"
              @select-all="handleSelectAll"
              size="small">
              <el-table-column
                type="selection">
              </el-table-column>
              <el-table-column min-width="220px" label="商品信息">
                <template slot-scope="scope">
                  <div class="ll-p-m-w">
                    <div class="ll-img-w">
                      <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                      <img v-else src="/shop/static/img_error.png" alt="">
                    </div>
                    <div class="ll-p-m-c-r">
                      <div>
                        {{scope.row.goodsName || '-'}}
                      </div>
                      <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                    </div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
              <el-table-column min-width="130px" label="活动商品名称">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.activityGoodsName" size="small" placeholder="输入名称"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="110px" label="拼团限制">
                <template slot-scope="scope">
                  <div>
                    <el-radio-group v-model="scope.row.limitGroupType">
                      <p><el-radio :label="0">全部</el-radio></p>
                      <p><el-radio :label="1">老用户</el-radio></p>
                      <p><el-radio :label="2">新用户</el-radio></p>
                    </el-radio-group>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="90px" prop="price" label="零售价"></el-table-column>
              <el-table-column min-width="110px" label="拼团价">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.groupPrice" size="small" placeholder="输入价格"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="110px" label="成团人数">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.groupCount" size="small" placeholder="输入人数"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="150px" label="限购数量(为空不限)">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.limitGoodsCount" size="small" placeholder="输入限购数量"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="110px" label="拼团库存">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.curStock" size="small" placeholder="输入库存"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="120px" label="成团时长(分钟)">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.activityTimes" size="small" placeholder="输入时长"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="110px" label="排序权重">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.sortNum" size="small" placeholder="输入权重"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column
                align="center"
                label="操作"
                fixed="right"
                width="80">
                <template slot-scope="scope" size="small">
                  <el-button @click="deleteGoodsItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div>
              <span style="color: red;">*</span>注意：权重排序，先创建的活动商品在前；剩余的按前端规则走。
            </div>
          </div>
        </el-form-item>

        <hr class="s-slip-tb">
        <el-form-item>
          <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
          <el-button size="small" @click="doCancelHandel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 选择门店 -->
    <choose-store ref="chooseStore" @confirmChooseStore="confirmChooseStore"></choose-store>
    <!-- 选择商品 -->
    <choose-goods ref="chooseGoods" @confirmChooseGoods="confirmChooseGoods"></choose-goods>
  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  import Storage from '@/utils/localStorage'
  import ChooseStore from "@/components/Activity/Common/ChooseStore"
  import ChooseGoods from "@/components/Activity/Common/ChooseGoodsFromLib"
  import Region from '@/components/Common/StoreCommon/Region'
  const reqUrl = {
    // 新增活动规则
    add: "/plateform/a/activityRule/add"
  }
  export default {
    data() {
      return {
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; // 如果没有后面的-8.64e7就是不可以选择今天的
          }
        }, 
        isDisabled: false,
        addParamMesg: {
          DIFFCITY: true,
          platformId: 5,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          activityType: '', // 活动类型
          activityTypeName: '', // 活动类型名称
          activityName: '', // 活动名称
          activityStartDate: '', // 活动开始时间
          activityEndDate: '', // 活动结束时间
          activityCode: '', // 活动编码
          // 参与门店
          activityStoreList: [],
          // 活动商品集合
          platCityGoodsInfos: [],
          deliveryType: 0, // 0 不支持配送 1  支持外卖配送   2  支持快递配送
          pickSelf: 0 // 0 不支持自提  1 支持自提
        },
        // 参与门店选中的
        activityStoresCheckedList: [],
        // 参与的选中的商品
        activityGoodsCheckedList: [],
        // 当前选中的商品用于批量设置价格
        checkedList: [],
        // 批量修改折扣
        discount: '',

        // 支持配送
        canDelivery: true,
        deliveryTypeTemp: 1, // 1 支持外卖配送   2 支持自提配送
        // 支持自提
        canPickSelf: false

      }
    },
    created() {
      // 活动类型 :11-拼团，21-秒杀
      let activityType = this.$route.params.activityType
      if(activityType == 11) {
        this.addParamMesg.activityType = activityType
        this.addParamMesg.activityTypeName = '拼团'
      }else if(activityType == 21) {
        this.addParamMesg.activityType = activityType
        this.addParamMesg.activityTypeName = '秒杀'
      }
    },
    mounted() {

    },
    components: {
      "choose-store": ChooseStore,
      "choose-goods": ChooseGoods,
      "region": Region
    },
    methods: {
      /**
       * [chooseStoreOpen 选择门店]
       * @return {[type]} [description]
       */
      chooseStoreOpen() {
        let _params = {
          title: '选择门店',
          platformId: this.addParamMesg.platformId,
          regionCode: this.addParamMesg.regionCode,
          checkedList: this.activityStoresCheckedList
        }
        this.$refs.chooseStore.setDefault(_params)
      },
      /**
       * [chooseGoodsOpen 选择商品]
       * @return {[type]} [description]
       */
      chooseGoodsOpen() {
        let _params = {
          title: '选择商品',
          regionCode: this.addParamMesg.regionCode,
          checkedList: this.activityGoodsCheckedList,
          searchSupplierGoods: 0
        }
        this.$refs.chooseGoods.setDefault(_params)
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.platformCityGoodsId, 'platformCityGoodsId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('platformCityGoodsId', row.platformCityGoodsId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
            return item
          }, [])
        }else {
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.activityGoodsCheckedList) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'platformCityGoodsId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : []
        }
      },
      /**
       * [confirmChooseStore 选中的门店]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseStore(_value) {
        let activityStoresCheckedList = []
        if(_value.checkedList && _value.checkedList.length) {
          activityStoresCheckedList = _value.checkedList
        }else {
          activityStoresCheckedList = []
        }
        this.activityStoresCheckedList = activityStoresCheckedList
      },
      /**
       * [confirmChooseGoods 选中的商品]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseGoods(_value) {
        let activityGoodsCheckedList = []
        if(_value.checkedList && _value.checkedList.length) {
          activityGoodsCheckedList = _value.checkedList
        }else {
          activityGoodsCheckedList = []
        }
        this.activityGoodsCheckedList = activityGoodsCheckedList
      },
      /**
       * [deleteItem 删除选中的门店]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteStoreItem(_row) {
        let erpStoreNo = _row.erpStoreNo
        this.activityStoresCheckedList = this.activityStoresCheckedList.filter(item => {
          return item.erpStoreNo !== erpStoreNo
        })
      },
      /**
       * [deleteItem 删除选中的商品]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteGoodsItem(_row) {
        let platformCityGoodsId = _row.platformCityGoodsId
        this.activityGoodsCheckedList = this.activityGoodsCheckedList.filter(item => {
          return item.platformCityGoodsId !== platformCityGoodsId
        })
      },
      /**
       * [setDiscountBatch 批量设置折扣]
       * @return {[type]} [description]
       */
      setDiscountBatch() {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        if(!this.discount) {
          this.$message("请输入拼团价折扣")
          return
        }else {
          if(!numReg.test(this.discount)) {
            this.$message("拼团价折扣为(0-100整数)")
            return
          }
        }
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return
        }
        let activityGoodsCheckedListTemp = JSON.parse(JSON.stringify(this.activityGoodsCheckedList))
        for(let i of activityGoodsCheckedListTemp) {
          for(let j of this.checkedList) {
            if(i.platformCityGoodsId === j.platformCityGoodsId) {
              i.groupPrice = ((parseFloat(this.discount) * parseFloat(i.price)) / 100).toFixed(2)
            }
          }
        }
        this.activityGoodsCheckedList = activityGoodsCheckedListTemp
        this.checkedList = []
      },
      /**
       * [doSaveHandel 保存]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        let salePriceOnePointReg = /((^[1-9]\d*))(\.\d{0,1}){0,1}$/

        if(!this.addParamMesg.activityName) {
          this.$message("请输入活动名称")
          return
        }else if(this.addParamMesg.activityName.length > 30) {
          this.$message("活动名称限制30个汉字")
          return
        }
        if(!this.addParamMesg.activityStartDate) {
          this.$message("请输入活动开始时间")
          return
        }
        if(!this.addParamMesg.activityEndDate) {
          this.$message("请输入活动结束时间")
          return
        }
        let activityStartDateNum = Date.parse(new Date(this.addParamMesg.activityStartDate))
        let activityEndDateNum = Date.parse(new Date(this.addParamMesg.activityEndDate))
        let currentDateNum = new Date().getTime()
        if(activityStartDateNum < currentDateNum) {
          this.$message("活动开始时间不能早于当前时间")
          return
        }
        if(activityStartDateNum > activityEndDateNum) {
          this.$message("活动结束时间不能早于开始时间")
          return
        }
        if(this.canDelivery) {
          this.addParamMesg.deliveryType = parseInt(this.deliveryTypeTemp)
        }else {
          this.addParamMesg.deliveryType = 0
        }
        if(this.canPickSelf) {
          this.addParamMesg.pickSelf = 1
        }else {
          this.addParamMesg.pickSelf = 0
        }
        if(!this.canPickSelf && !this.canDelivery) {
          this.$message("请选择配送方式")
          return
        }
        this.addParamMesg.activityStoreList = []
        if(this.activityStoresCheckedList && this.activityStoresCheckedList.length) {
          for(let item of this.activityStoresCheckedList) {
            this.addParamMesg.activityStoreList.push(item.erpStoreNo)
          }
        }
        if(this.addParamMesg.activityStoreList.length === 0) {
          this.$message("请选择参与门店")
          return
        }
        if(this.activityGoodsCheckedList && this.activityGoodsCheckedList.length) {
          for(let item of this.activityGoodsCheckedList) {
            if(!item.activityGoodsName) {
              this.$message(`请输入(${item.goodsName})的活动商品名称`)
              return
            }else if (item.activityGoodsName.length > 30) {
              this.$message(`请输入(${item.goodsName})的活动商品名称限制30个汉字`)
              return
            }
            if(!item.groupPrice) {
              this.$message(`请输入(${item.goodsName})的拼团价`)
              return
            }else if(!salePriceReg.test(item.groupPrice)) {
              this.$message(`(${item.goodsName})的拼团价是大于0，小数保留小数点后2位`)
              return
            }else {
              if(parseFloat(item.groupPrice) == '0.00') {
                this.$message(`(${item.goodsName})的拼团价是大于0，小数保留小数点后2位`)
                return
              }
            }
            if(parseFloat(item.groupPrice) > item.price) {
              this.$message(`(${item.goodsName})的拼团价必须小于或等于零售价`)
              return
            }
            if(!item.groupCount) {
              this.$message(`请输入(${item.goodsName})的成团人数`)
              return
            }else if(!numReg.test(item.groupCount)) {
              this.$message(`(${item.goodsName})的成团人数为大于零的正整数`)
              return
            }
            if(item.limitGoodsCount) {
              if(!numReg.test(item.limitGoodsCount)) {
                this.$message(`(${item.goodsName})的限购数量为大于零的正整数`)
                return
              }
            }
            if(!item.curStock) {
              this.$message(`请输入(${item.goodsName})的拼团库存`)
              return
            }else if(!numReg.test(item.curStock)) {
              this.$message(`(${item.goodsName})的拼团库存为大于零的正整数`)
              return
            }
            if(!item.activityTimes) {
              this.$message(`请输入(${item.goodsName})的成团时长`)
              return
            }else if(!numReg.test(item.activityTimes)) {
              this.$message(`(${item.goodsName})的成团时长为大于零的正整数`)
              return
            }
            if(!item.sortNum) {
              this.$message(`请输入(${item.goodsName})的排序权重`)
              return
            }else {
              if(!numReg.test(item.sortNum)) {
                this.$message(`(${item.goodsName})的排序权重为1—100正整数`)
                return
              }else {
                if(item.sortNum < 1 || item.sortNum > 100) {
                  this.$message(`(${item.goodsName})的排序权重为1—100正整数`)
                  return
                }
              }
            }
            item.activityStartDate = this.addParamMesg.activityStartDate
            item.activityEndDate = this.addParamMesg.activityEndDate
          }
          this.addParamMesg.platCityGoodsInfos = this.activityGoodsCheckedList
        }
        if(this.addParamMesg.platCityGoodsInfos.length === 0) {
          this.$message("请选择参与商品")
          return
        }
        this.isDisabled = true
        this.$http.post(reqUrl.add, {...this.addParamMesg}).then(res => {
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("保存成功")
            this.$router.push('/store_act')
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/store_act')
        }).catch(() => {})
      }
    }
  }

</script>



